// src/components/ProductTable.tsx
const produtos = [
    { nome: "EcoTupper", tipo: "Garrafa", cor: "Rosa", volume: "310ml" },
    { nome: "Cristalwave", tipo: "Pote", cor: "Vinho", volume: "1,2L" },
    // ...outros
  ];
  
  export default function ProductTable() {
    return (
      <table className="w-full border border-gray-300">
        <thead className="bg-gray-200">
          <tr>
            <th className="text-left p-2">Nome</th>
            <th className="text-left p-2">Tipo</th>
            <th className="text-left p-2">Cor</th>
            <th className="text-left p-2">Volume</th>
          </tr>
        </thead>
        <tbody>
          {produtos.map((p, i) => (
            <tr key={i} className="border-t border-gray-300">
              <td className="p-2">{p.nome}</td>
              <td className="p-2">{p.tipo}</td>
              <td className="p-2">{p.cor}</td>
              <td className="p-2">{p.volume}</td>
            </tr>
          ))}
        </tbody>
      </table>
    );
  }
  